/**
 * Filesystem utility functions
 *
 * Shared helpers for file and directory operations
 *
 * Note: This module intentionally has no dependencies on logger.ts
 * to avoid circular dependencies (logger.ts uses ensureDirectory).
 */

import { mkdir, stat } from "node:fs/promises";

/**
 * Sanitize a string for safe use in filenames across all platforms.
 * Replaces characters illegal in Windows filenames (\ / : * ? " < > |) with underscores.
 */
export function sanitizeForFilename(input: string): string {
  return input.replace(/[\\/:*?"<>|]/g, "_");
}

/**
 * Ensure directory exists, creating it if necessary
 * Creates parent directories recursively with secure permissions (mode 0o700)
 *
 * @param dirPath - Path to the directory to ensure exists
 */
export async function ensureDirectory(dirPath: string): Promise<void> {
  try {
    await stat(dirPath);
  } catch {
    await mkdir(dirPath, { recursive: true, mode: 0o700 });
  }
}
